<style scoped>
.Sider_uls {
  height: 100%;
}
.fls {
  display: flex;
  flex-direction: row;
  height: 100%;
}
.content_text {
  width: 100%;
  padding: 40px;
}
</style>
<template>
  <div style="height: 100%">
    <div class="fls">
      <!-- 左侧 侧边栏 -->
      <div>
        <Menu
          @on-open-change="handleOpen"
          @on-select="handleSelect"
          :active-name="change_Item"
          :open-names="['1']"
          class="Sider_uls"
        >
          <Submenu name="1">
            <template slot="title">
              <Icon type="ios-people" />用户管理
            </template>
            <MenuItem name="1-1">用户列表</MenuItem>
            <MenuItem name="1-2">新增用户</MenuItem>
          </Submenu>
          <Submenu name="2">
            <template slot="title">
              <Icon type="ios-paper" />工具
            </template>
            <MenuItem name="2-1">mockTools</MenuItem>
          </Submenu>
          <MenuItem name>退出</MenuItem>
        </Menu>
      </div>
      <!-- 右侧内容 -->
      <div class="content_text">
        <div v-if="change_Item === '1-1'">
          <user-list></user-list>
        </div>
        <div v-if="change_Item === '1-2'">
          <create-user @createdUser="handleAddUser"></create-user>
        </div>
      </div>
    </div>
    <footers></footers>
  </div>
</template>
<script>
import userList from "../components/users/user_list";
import createUser from "../components/users/create_user";
import footers from "../components/footer";
export default {
  data() {
    return {
      theme3: "light",
      change_Menu: null,
      change_Item: "1-1"
    };
  },
  components: {
    userList,
    createUser,
    footers
  },
  methods: {
    handleOpen(v) {},
    handleSelect(v) {
      console.log("mennn", v);
      if (v) {
        this.change_Item = v;
      } else {
        this.Logout();
      }
    },
    Logout() {
      this.$http({
        url: "/api/Logout",
        method: "get"
      })
        .then(res => {
          console.log("logout", res.data);
          setTimeout(() => {
            this.$router.push("/login")
          }, 500)
        })
        .catch(err => {
          this.$Message.error("退出失败");
        });
    },
    handleAddUser() {
      this.change_Item = "1-1";
    }
  },
  watch: {
    change_Menu: {
      handler(n) {
        console.log("change_Menu", n);
      },
      deep: true
    }
  }
};
</script>
