<style scoped>
.pas {
  margin-top: 10px;
}
</style>
<template>
  <div>
    <Table border :columns="columns12" :data="data6">
      <template slot-scope="{ row }" slot="name">
        <strong>{{ row.name }}</strong>
      </template>
      <template slot-scope="{ row, index }" slot="action">
        <!-- <Button type="primary" size="small" style="margin-right: 5px" @click="show(index)">View</Button> -->
        <Button type="error" size="small" @click="remove(index, row)">删除</Button>
      </template>
    </Table>
    <Page
      @on-change="changePage"
      class="pas"
      :current="pages"
      :page-size="page_size"
      :total="totals"
      simple
    />
  </div>
</template>
<script>
import { log } from "util";
export default {
  data() {
    return {
      columns12: [
        {
          title: "用户",
          slot: "name"
        },
        {
          title: "手机",
          key: "phone"
        },
        {
          title: "邮件",
          key: "email"
        },
        {
          title: "操作",
          slot: "action",
          width: 150,
          align: "center"
        }
      ],
      data6: [],
      totals: 0,
      pages: 1,
      page_size: 10
    };
  },
  created() {
    this.getUserList({ page: this.pages, pageSize: this.page_size });
  },
  methods: {
    getUserList(opt) {
      this.$http({
        method: "get",
        url: "/api/getUser?page=" + opt.page + "&pageSize=" + opt.pageSize
      })
        .then(res => {
          this.totals = res.data.total;
          this.data6 = [];
          res.data.data.filter((v, i) => {
            this.data6.push(v);
          });
        })
        .catch(err => {
          console.log("getUser err", [err]);
          if (err.response.data.status === 403) {
            this.$router.push("/login");
            return;
          }
          this.data6 = [];
        });
    },
    delUser(str) {
      this.$http({
        method: "delete",
        url: "/api/delUser?name=" + str
      })
        .then(res => {
          this.$Message.success(`"${str}"用户 删除成功`);
          this.getUserList({ page: this.pages, pageSize: this.page_size });
        })
        .catch(err => {
          if (err.response.data.status === 403) {
            this.$router.push("/login");
            return;
          }
          this.$Message.error({
            content: err.response.data.msg,
            top: 100
          });
        });
    },
    show(index) {
      // this.$Modal.info({
      //   title: "User Info",
      //   content: `Name：${this.data6[index].name}<br>Age：${this.data6[index].phone}<br>Address：${this.data6[index].email}`
      // });
    },
    remove(index, row) {
      console.log("remove", row);
      this.delUser(row.name);
      // this.data6.splice(index, 1);
    },
    changePage(v) {
      this.pages = v;
      this.getUserList({ page: v, pageSize: this.page_size });
    }
  },
  watch: {}
};
</script>